<template>
<div>
  <base-dialog 
    :show="!!error"
    title="An Error Occurred!"
    fixed>
      <p>{{ error }}</p>
    </base-dialog>
  <base-dialog 
    :show="isLoading" 
    title="Authenticating..." 
    fixed>
    <base-spinner></base-spinner>
  </base-dialog>
  <base-card>
    <form @submit.prevent="submitForm">
      <div class="form-control">
        <label for="email">eMail</label>
        <input 
          type="email" 
          id="email"
          v-model.trim="email" />
      </div>
      <div class="form-control">
        <label for="password">Password</label>
        <input 
          type="password"
          id="password"
          v-model="password" />
      </div>
      <p v-if="!formIsValid">
        Please enter at least 6 character password and your email must include the '@' sign!
      </p>
      <base-button>{{ submitButtonCaption }}</base-button>
      <base-button 
        type="button" 
        mode="flat"
        @click="switchAuthMode"
        >{{ switchModeButtonCaption }}</base-button>
    </form>
</base-card>
</div>  
</template>

<script>
export default {
  data() {
    return {
      email: '',
      password: '',
      formIsValid: true,
      mode: 'login',
      isLoading: false,
      error: null
    }
  },
  computed: {
    submitButtonCaption() {
      if (this.mode === 'login') {
        return 'Login';
      } else {
        return 'Signup';
      }
    },
    switchModeButtonCaption() {
      if (this.mode === 'login') {
        return 'Signup instead';
      } else {
        return 'Login instead';
      }
    }
  },
  methods: {
    async submitForm() {
      this.FormIsValid = true;
      if (this.email === '' ||
          this.email.includes(!'@') ||
          this.password.length < 6) {
        this.FormIsValid = false;
        return;
      }

      this.isLoading = true;
      const activePayload = {
        email: this.email,
        password: this.password
      }

      try {
        if (this.mode ==='login') {
          await this.$store.dispatch('login', activePayload); 
        }
        else {
          await this.$store.dispatch('signup', activePayload);
        }
        const redirectUrl = '/'+ (this.$route.query.redirect || 'coaches');
        console.log('redirectUrl:'+redirectUrl);
        this.$router.replace(redirectUrl);
      } catch (err) {
        this.error = err.message || 'Failed to authenticate, try later.';
        console.log("error in submitform - error:"+this.error);
      }
      this.isLoading = false;
    },
    switchAuthMode() {
      if (this.mode === 'login') {
        this.mode = 'signup';
      } else {
        this.mode = 'login';
      }
    },
    handleError() {
      this.error = null;
    }
  }
}
</script>
<style scoped>
form {
  margin: 1rem;
  padding: 1rem;
}

.form-control {
  margin: 0.5rem 0;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  padding: 0.15rem;
}

input:focus,
textarea:focus {
  border-color: #3d008d;
  background-color: #faf6ff;
  outline: none;
}

.errors {
  font-weight: bold;
  color: red;
}

.actions {
  text-align: center;
}
</style>